.header__title {
  display: block;
  margin: 50px auto 20px;
  width: 300px;
  font-family: 'Courier New', Courier, monospace;
  border: 2px solid black;
  background-color: rgb(245, 245, 78);
}

@media (max-width: 800px) {
  .header__title {
    width: 150px;
    margin: 10px auto 10px;
    font-size: 25px;
  }
}
